.btn {
  display: inline-block;
  padding: 10px 15px;
  border-radius: 5px;
  background-color: green;
  text-align: center;
  font-size: 16px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 500ms;
  font-family: "Comic Sans MS", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.btn.hide {
  font-size: 0;
  padding: 0;
}

.timer-wrapper {
  margin: 20px auto;
  padding: 20px;
  width: 400px;
  border-radius: 10px;
  background-color: grey;
  box-shadow: 0 0 9px green;
  font-family: "Comic Sans MS";
}

.timer-wrapper h2 {
  text-align: center;
  text-shadow: 0 0 3px green;
}

.clock-face {
  margin: 0 auto;
  padding: 10px 15px;
  background-color: #fff;
  width: 300px;
  min-height: 60px;
  border-radius: 5px;
  text-align: center;
  font-size: 36px;
  letter-spacing: 5px;
  color: black;
  text-shadow: 0 0 3px green;
  font-weight: 900;
}

.timer-btn-wrapper {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.timer-btn {
  margin: 0 10px;
  display: inline-block;
  padding: 10px 15px;
  min-width: 100px;
  font-size: 20px;
  font-weight: 700;
  text-shadow: 0 0 2px #fff;
  text-align: center;
  background-color: green;
  border-radius: 5px;
  box-shadow: 0 0 6px black;
  cursor: pointer;
}
